body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    background-color: #009933;
    color: white;
    width: 100%;
}

.instruction-header {
    background-color: #ffcc33;
    color: white;
    width: 100%;
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
}

.game-container {
    text-align:center;
    margin-top: 2rem;
}

@media only screen and (max-width: 1460px) {
    .game-square {
      height: 68px;
      width: 102px;
    }
  }

@media only screen and (max-width: 1175px) {
.game-square {
    height: 50px;
    width: 75px;
}
}

@media only screen and (max-width: 875px) {
.game-square {
    height: 40px;
    width: 60px;
}
}

@media only screen and (max-width: 740px) {
    .game-square {
      height: 28px;
      width: 42px;
    }
  }

@media only screen and (max-width: 595px) {
.game-square {
    height: 20px;
    width: 30px;
}
}

@media only screen and (max-width: 500px) {
    .game-square {
      height: 18px;
      width: 27px;
    }
  }

@media only screen and (max-width: 480px) {
.game-square {
    height: 44px;
    width: 66px;
}
}

.game-square {
    margin: 1rem;
    display: inline-block;
}
.footer {
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    background-color: #009933;
    color: white;
    width: 100%;
    position: fixed;
    bottom: 0;
}
